import './app.sass';


// import { request } from './request.js';
const request = () => {};

const $tweets = document.querySelector('.tweets');
const now = Date.now();

window.addEventListener('dragover', event => {
	event.preventDefault();
});
window.addEventListener('drop', event => {
	event.preventDefault();
});
function render(tweets) {
	$tweets.innerHTML = '';
	tweets
		// .filter(({img, sendAfter }) => img && (now < new Date(sendAfter)) )
		.forEach((tweet, index) => {
			const img = tweet.img ? `<img src="./img/${tweet.img}"/>` : '';
			$tweets.innerHTML += `<div class="card-1 ${tweet.ready ? 'ready' : ''}" data-id="${index}">
						<div class="full-screen">↗</div>
						<div class="sendAfter">${tweet.sendAfter}</div>
						<div class="status" contenteditable="true" data-id="${index}">${tweet.status}</div>
						${img}
					</div>`;
		});
	[...document.querySelectorAll('[contenteditable]')].forEach(el => {
		el.addEventListener('input', event => {
			request({
				url: 'save',
				method: 'POST',
				body: JSON.stringify({ id: event.target.dataset.id, status: event.target.innerHTML }),
			}).then(data => {
				console.log('send success');
				console.log('event.target', event.target.dataset.id, event.target.innerHTML);
			});
		});
	});
	[...document.querySelectorAll('.tweets > div')].forEach(el => {
		el.addEventListener('dblclick', event => {
			request({
				url: 'save',
				method: 'POST',
				body: JSON.stringify({
					id: event.target.dataset.id || 0,
					ready: !event.currentTarget.classList.contains('ready'),
				}),
			}).then(tweets => {
				render(JSON.parse(tweets));
			});
		});
		el.addEventListener('dragenter', event => {
			el.classList.add('drag-hover');
		});
		el.addEventListener('dragleave', event => {
			el.classList.remove('drag-hover');
		});
	});
	[...document.querySelectorAll('.card-1')].forEach(holder => {
		if ('draggable' in document.createElement('span')) {
			holder.ondragover = () => {
				this.className = 'hover';
				return false;
			};
			holder.ondragend = () => {
				this.className = '';
				return false;
			};
			holder.ondrop = e => {
				this.className = '';
				e.preventDefault();
				readfiles(e.dataTransfer.files);
			};
		} else {
			fileupload.className = 'hidden';
			fileupload.querySelector('input').onchange = function() {
				readfiles(this.files);
			};
		}
	});
}

const socket = io('http://localhost:3000');
socket.on('tweets.json', (tweets) => {
	render(tweets.filter(({ sendAfter }) => (now < new Date(sendAfter)) ));
})

const tests = {
	filereader: typeof FileReader != 'undefined',
	dnd: 'draggable' in document.createElement('span'),
	formdata: !!window.FormData,
	progress: 'upload' in new XMLHttpRequest(),
};

function readfiles(files) {
	debugger;
	var formData = tests.formdata ? new FormData() : null;
	// for (var i = 0; i < files.length; i++) {
	//   if (tests.formdata) formData.append('file', files[i]);
	//   previewfile(files[i]);
	// }

	// now post a new XHR request
	if (tests.formdata) {
		var xhr = new XMLHttpRequest();
		xhr.open('POST', '/devnull.php');
		xhr.onload = function() {
			progress.value = progress.innerHTML = 100;
		};

		if (tests.progress) {
			xhr.upload.onprogress = function(event) {
				if (event.lengthComputable) {
					var complete = (event.loaded / event.total * 100) | 0;
					progress.value = progress.innerHTML = complete;
				}
			};
		}

		xhr.send(formData);
	}
}



